import React, { useEffect, useState } from "react";
import { Actor, HttpAgent } from "@dfinity/agent";
import { idlFactory } from "../../../declarations/nft";
import { idlFactory as tokenIdlFactory } from "../../../declarations/token";
import { Principal } from "@dfinity/principal";
import Button from "./Button";
import { CryptoCanvas } from "../../../declarations/CryptoCanvas";
import CURRENT_USER_ID from "../index";
import PriceLabel from "./PriceLabel";
function Item(props) {
    const [name, setName] = useState();
    const [owner, setOwner] = useState();
    const [image, setImage] = useState();
    const [button, setButton] = useState();
    const [priceInput, setPriceInput] = useState();
    const [loaderHidden, setLoaderHidden] = useState(true);
    const [blur, setBlur] = useState();
    const [sellStatus, setSellStatus] = useState("");
    const [priceLabel, setPriceLabel] = useState();
    const [shouldDisplay, setDisplay] = useState(true);
    const id = props.id;
    const localHost = "http://localhost:8080/";
    const agent = new HttpAgent({ host: localHost });
    //ToDo: remove the following line when deploy live
    agent.fetchRootKey();
    let NFTActor;
    async function loadNFT() {
        NFTActor = await Actor.createActor(idlFactory, {
            agent,
            canisterId: id,
        });
        const name = await NFTActor.getName();
        const owner = await NFTActor.getOwner();
        const imageData = await NFTActor.getAsset();
        const imageContent = new Uint8Array(imageData);
        const image = URL.createObjectURL(new Blob([imageContent.buffer], { type: "image/png" }));
        setName(name);
        setOwner(owner.toText());
        setImage(image);
        if (props.role == "collection") {
            const nftIsListed = await CryptoCanvas.isListed(props.id);
            if (nftIsListed) {
                setOwner("CryptoCanvas");
                setBlur({ filter: "blur(4px)" });
                setSellStatus("Listed");
            }
            else {
                setButton(React.createElement(Button, { handleClick: handleSell, text: "Sell" }));
            }
        }
        else if (props.role == "discover") {
            const originalOwner = await CryptoCanvas.getOriginalOwner(props.id);
            if (originalOwner.toText() != CURRENT_USER_ID.toText()) {
                setButton(React.createElement(Button, { handleClick: handleBuy, text: "Buy" }));
            }
            const price = await CryptoCanvas.getListedNFTPrice(props.id);
            setPriceLabel(React.createElement(PriceLabel, { sellPrice: price.toString() }));
        }
    }
    useEffect(() => {
        loadNFT();
    }, []);
    let price;
    function handleSell() {
        console.log("Sell clicked!");
        setPriceInput(React.createElement("input", { placeholder: "Price in Tokens", type: "number", className: "price-input", value: price, onChange: (e) => price = e.target.value }));
        setButton(React.createElement(Button, { handleClick: sellItem, text: "Confirm" }));
    }
    async function sellItem() {
        setBlur({ filter: "blur(4px)" });
        setLoaderHidden(false);
        console.log("set price = " + price);
        const listingResult = await CryptoCanvas.listItem(props.id, Number(price));
        console.log("listing: " + listingResult);
        if (listingResult == "Success") {
            const cryptoCanvasId = await CryptoCanvas.getCryptoCanvasCanisterID();
            const transferResult = await NFTActor.transferOwnership(cryptoCanvasId);
            console.log("transfer: " + transferResult);
            if (transferResult == "Success") {
                setLoaderHidden(true);
                setButton();
                setPriceInput();
                setOwner("CryptoCanvas");
                setSellStatus("Listed");
            }
        }
    }
    async function handleBuy() {
        console.log("Buy was triggered");
        setLoaderHidden(false);
        const tokenActor = await Actor.createActor(tokenIdlFactory, {
            agent,
            canisterId: Principal.fromText("qjdve-lqaaa-aaaaa-aaaeq-cai"), //<REPLACE WITH YOUR TOKEN CANISTER ID>
        });
        const sellerId = await CryptoCanvas.getOriginalOwner(props.id);
        const itemPrice = await CryptoCanvas.getListedNFTPrice(props.id);
        const result = await tokenActor.transfer(sellerId, itemPrice);
        console.log(result);
        if (result == "Success") {
            const transferResult = await CryptoCanvas.completePurchase(props.id, sellerId, CURRENT_USER_ID);
            console.log("purchase: " + transferResult);
            setLoaderHidden(true);
            setDisplay(false);
        }
    }
    return (React.createElement("div", { style: { display: shouldDisplay ? "inline" : "none" }, className: "disGrid-item" },
        React.createElement("div", { className: "disPaper-root disCard-root makeStyles-root-17 disPaper-elevation1 disPaper-rounded" },
            React.createElement("img", { className: "disCardMedia-root makeStyles-image-19 disCardMedia-media disCardMedia-img", src: image, style: blur }),
            React.createElement("div", { className: "lds-ellipsis", hidden: loaderHidden },
                React.createElement("div", null),
                React.createElement("div", null),
                React.createElement("div", null),
                React.createElement("div", null)),
            React.createElement("div", { className: "disCardContent-root" },
                priceLabel,
                React.createElement("h2", { className: "disTypography-root makeStyles-bodyText-24 disTypography-h5 disTypography-gutterBottom" },
                    name,
                    React.createElement("span", { className: "purple-text" },
                        " ",
                        sellStatus)),
                React.createElement("p", { className: "disTypography-root makeStyles-bodyText-24 disTypography-body2 disTypography-colorTextSecondary" },
                    "Owner: ",
                    owner),
                priceInput,
                button))));
}
export default Item;
